<template>
  <Alert :arrayAlert="arrayAlert" v-on:changeShow="closeAlert($event)" />
  <div class="login-page">
    <div class="container-login" @keydown.enter="btnLogin">
      <h2 class="title-page">
        {{ getLocalizationtxt.userPage.authorization }}
      </h2>
      <div class="row-input row-email">
        <input
          type="email"
          v-model="email"
          @input="handlingFormInput($event, 'email', 'email', 'errorEmail', 5)"
          @paste="eventStop"
          class="input-form"
          v-bind:placeholder="getLocalizationtxt.userPage.email"
          ref="inputEmail"
        />
        <p :class="errorEmail" class="error-input email">
          {{ getLocalizationtxt.userPage.emailError }}
        </p>
      </div>
      <div class="row-input row-password">
        <input
          type="password"
          v-model="password"
          @input="
            handlingFormInput(
              $event,
              'password',
              'password',
              'errorPassword',
              5
            )
          "
          @paste="eventStop"
          class="input-form"
          maxlength="32"
          v-bind:placeholder="getLocalizationtxt.userPage.password"
          ref="inputPassword"
        />
        <img
          src="@/assets/icons/eye.svg"
          class="icon-eye"
          @click="changeInputType"
        />
        <p :class="errorPassword" class="error-input password">
          {{ getLocalizationtxt.userPage.passwordError }}
        </p>
      </div>
      <button class="btn-form blue" @click="btnLogin">
        {{ getLocalizationtxt.userPage.send }}
      </button>
    </div>
  </div>
</template>

<script>
import Alert from "@/components/Alert";
import { emptyValidation, emailValidation } from "@/plugins/Validation";

export default {
  name: "Login",
  components: { Alert },
  data() {
    return {
      email: "",
      errorEmail: "",
      password: "",
      errorPassword: "",
      loginClicked: true,
      logged: false,
      arrayAlert: {
        msg: "",
        msgType: "",
        show: false,
      },
    };
  },
  computed: {
    getLocalizationtxt() {
      return this.$store.getters["localizationInfo"];
    },
  },
  mounted() {
    this.setAutofocus();
  },
  methods: {
    setAutofocus() {
      this.$refs.inputEmail.focus();
    },
    handlingFormInput(event, typeValidation, vModel, errorName, minLength) {
      let value = event.target.value;
      if (typeValidation === "email") {
        let errorCheckedValidation = emailValidation(this[vModel]);
        if (errorCheckedValidation) this[errorName] = "true";
        else this[errorName] = "false";
      } else if (typeValidation === "password") {
        let errorCheckedEmpty = emptyValidation(value, minLength);
        if (errorCheckedEmpty) this[errorName] = "true";
        else this[errorName] = "false";
      }
    },
    eventStop(event) {
      event.preventDefault();
    },
    changeInputType() {
      if (this.$refs.inputPassword.type == "password")
        this.$refs.inputPassword.type = "text";
      else this.$refs.inputPassword.type = "password";
    },
    btnLogin() {
      if (this.loginClicked) {
        if (this.errorEmail === "true" && this.errorPassword === "true") {
          const data = {
            id: null,
            name: this.name,
            phone: this.phone,
            email: this.email,
            password: this.password,
          };
          this.$store.dispatch("A_CHECK_LOGIN", data);

          this.checkLogin();
        } else if (this.errorEmail === "" || this.errorPassword === "") {
          this.arrayAlert.show = true;
          this.arrayAlert.msg = this.getLocalizationtxt.alertPage.empty_info;
          this.arrayAlert.msgType = "error";
        }
        this.loginClicked = false;
        setTimeout(() => {
          this.loginClicked = true;
        }, 1000);
      }
    },
    checkLogin() {
      this.logged = this.$store.getters["checkLogin"];
      if (this.logged) {
        this.$router.push("/persons");
      } else {
        this.arrayAlert.show = true;
        this.arrayAlert.msg = this.getLocalizationtxt.alertPage.error_login;
        this.arrayAlert.msgType = "error";
      }
    },
    closeAlert(data) {
      this.arrayAlert.show = data;
      this.arrayAlert.msg = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.login-page {
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;

  .container-login {
    border: solid 1px #dee2e6;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 35px;
    animation: openForm 0.5s;

    @keyframes openForm {
      0% {
        transform: translate(20%, 0px);
      }
      100% {
        transform: translate(0px, 0px);
      }
    }

    .title-page {
      margin-bottom: 20px;
    }

    .icon-eye {
      width: 15px;
      cursor: pointer;
      position: absolute;
      top: 7px;
      right: 5px;
    }
  }
}
</style>
